p {
  font-size: 1.3rem;
  margin: 5px;
}

math {
  margin: 5px;
  font-size: 1.3rem;
}

.post {
  width: 100%;
  padding: 15px;
}

/* Other Non-Flexbox Related Styles */
.c-header,
.c-footer {
  background: #edd382;
  text-align: center;

  font-family: "Source Sans Pro", sans-serif;
  font-size: 1.4em;
  font-weight: 300;
  line-height: 1.6em;
  padding: 10px;
  p {
    font-weight: 300;
  }

  h1 {
    text-transform: uppercase;
    font-weight: 900;
  }
}
.c-footer {
  bottom: 0;
  padding: 10px;
  width: 100%;
}

body {
  font-family: "Georgia", Times, serif;
  line-height: 1.6em;
  padding: 0;
  margin: 0;
  width: 100%;
}

span {
  text-align: center;
}

form {
  font-size: 1.2rem;
  display: grid;
  align-items: center;
  gap: 1rem;

  background-color: #edd382;
  border-radius: 7px;
  margin: 10px;
}

.form-viz {
  grid-template-columns: 1fr 12rem;
  padding: 3.2rem 4rem;
}

label {
  font-weight: 500;
}

label::first-letter {
  display: inline-block;
  margin-right: 4px;
  font-size: 1.3rem;
}

input {
  width: 120px;
}

input,
select {
  font-family: inherit;
  color: inherit;
  font-size: 1.3rem;
  padding: 0.3rem;
  text-align: center;
  border: 1px solid var(--color-light);
  border-radius: 4px;
  transition: 0.3s;
}

input:focus,
select:focus {
  outline: none;
  border: 1px solid var(--color-dark);
}

form .button {
  margin-top: 0.6rem;
  grid-column: 2;
}

form h2 {
  grid-column: 1 / -1;
  font-size: 2.2rem;
  text-transform: uppercase;
  letter-spacing: -0.5px;
  margin-bottom: 1.6rem;
}

.plot {
  padding-bottom: 30px;
}
